<template>
    <div class="competitor">
        <div class="competitor_1" :style="{transform:competitor_1_translate}"><span>{{ hexagons[2].competitor_1 }}</span></div>
        <div class="competitor_2" :style="{transform:competitor_2_translate}"><span>{{ hexagons[2].competitor_2 }}</span></div>
    </div>
    <div class="main-hexagons">
        <div class="hexagons">
            <div class="hexagon" v-for="(hexagon, index) in hexagons" :key="hexagon.id" @click="center(index)"><span class="place">{{ hexagon.place }}</span><span class="date">{{ hexagon.date }}</span><span class="time">{{ hexagon.time }}</span> <button class="buy_tickets">Купить билет</button></div>
        </div>
    </div>
</template>
<script>
export default {
    name: "my-hexagon",
    data() {
        return {
            hexagons:[
            {id:1, place:'Стадион', time:'17:00', date:'30 мая', competitor_1: 'Соперник-1', competitor_2:'Соперник-2'},
            {id:2, place:'Стадион', time:'15:00', date:'17 июня', competitor_1: 'Соперник-3', competitor_2:'Соперник-4'},
            {id:3, place:'Стадион', time:'13:00', date:'26 июня', competitor_1: 'Соперник-5', competitor_2:'Соперник-6'},
            {id:4, place:'Стадион', time:'16:00', date:'16 июля', competitor_1: 'Соперник-7', competitor_2:'Соперник-8'},
            {id:5, place:'Стадион', time:'19:00', date:'30 сентября', competitor_1: 'Соперник-9', competitor_2:'Соперник-10' },
            ],
        competitor_1_translate:'translateX(-100%)',
        competitor_2_translate:'translateX(100%) rotate(180deg)'
        }
    },
    mounted() {
        document.addEventListener('mousewheel', this.List);
        document.addEventListener('touchmove', this.List);
        setTimeout(() => this.competitor_1_translate = 'translateX(0%)', 100);
        setTimeout(() => this.competitor_2_translate = 'translateX(0%) rotate(180deg)', 100);
    },
    methods: {
        List (wheel){
        document.querySelector('.main-hexagons').removeEventListener("wheel", this.List); 
            if (wheel.deltaY > 0) {
                this.hexagons.unshift(this.hexagons.pop());
                this.animateHexagons();

            }else {
                this.hexagons.push(this.hexagons.shift());
                this.animateHexagons();
                }       
            },
        animateHexagons(){
            this.competitor_1_translate = 'translateX(-100%)'
            this.competitor_2_translate = 'translateX(100%) rotate(180deg)'
            setTimeout(() => this.competitor_1_translate = 'translateX(0%)', 750);
            setTimeout(() => this.competitor_2_translate = 'translateX(0%) rotate(180deg)', 750); 
            document.removeEventListener('mousewheel', this.List);
            document.removeEventListener('touchmove', this.List);
            setTimeout(() => {
                document.addEventListener('mousewheel', this.List);
                document.addEventListener('touchmove', this.List);
            }, 1500);
        },
        center: function(index) {
            if(index != 2) {
            this.hexagons[index] = this.hexagons.splice(2, 1, this.hexagons[index])[0];
            this.competitor_1_translate = 'translateX(-100%)'
            this.competitor_2_translate = 'translateX(100%) rotate(180deg)'
            setTimeout(() => this.competitor_1_translate = 'translateX(0%)', 750);
            setTimeout(() => this.competitor_2_translate = 'translateX(0%) rotate(180deg)', 750);
        }
    }
}
}
</script>
<style>
.hexagons {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    position: relative;
    z-index: 1;
    height: 100%;
}

.hexagon {
    background-image: url(../../img/hexagon.svg);
    background-position: center;
    color: #000;
    background-size: cover;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    transition: all 0.7s ease;
    display: flex;
    flex-direction: column;
    gap: 0.5vw;
    cursor: pointer;
}

.hexagon:nth-child(1), .hexagon:nth-child(5) {
    width: 8vw;
    height: 8vw;
}

.hexagon:nth-child(2), .hexagon:nth-child(4) {
    width: 12vw;
    height: 12vw;
}

.hexagon:nth-child(3) {
    width: 18vw;
    height: 18vw;
}

.main-hexagons {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-self: center;
    align-items: center;
}

.hexagon:nth-child(1) {
    transform: translate(-25vw, 15vw);
}

.hexagon:nth-child(2) {
    transform: translate(-15vw, 9vw);
}

.hexagon:nth-child(4) {
    transform: translate(15vw, -9vw);
}

.hexagon:nth-child(5) {
    transform: translate(25vw,-15vw);
}

.competitor {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
}

.competitor_1,  .competitor_2{
    position: absolute;
    width: 40vw;
    height: 7.45vw;
    background-color: #fff;
    clip-path: polygon(0 0, 0 100%, 90.5% 100%, 100% 0%, 100% 0);
    bottom: 50%;
    z-index: 1;
    transition: all 1s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0px 5px 45px 0px;
    font-size: 3vw;
    font-weight: bold;
}

.date, .place, .buy_tickets {
    display: none;
}

.hexagon:nth-child(3):hover .date, .hexagon:nth-child(3):hover .time, .hexagon:nth-child(3):hover .place, .hexagon:nth-child(3):hover .buy_tickets {
    display: flex;
}

.hexagon:nth-child(3) .date {
    font-weight: bold;
}

.competitor_2 {
    top:50vh;
    right: 0;
}

.competitor_2 span {
    transform: rotate(180deg);
}

.place {
    font-size: 1.5vw;
}

.time {
    font-size: 1.5vw;
}

.date {
    font-size: 2.3vw;
}

.buy_tickets {
    background: none;
    border: 1px solid #000;
    justify-content: center;
    padding: 0.6vw;
    font-size: 1vw;
    border-radius: 5px;
    cursor: pointer;
}

@media (max-width:1770px) {
    .big-circle {
        height: 60vw;
    }
}
@media (max-width:650px) {
    .big-circle {
        width: 100vw;
        height: 100vw;
    }

    .small-circle {
        height: 60vw;

    }

    .hexagon:nth-child(1), .hexagon:nth-child(5) {
        width: 15vw;
        height: 15vw;
    }

    .hexagon:nth-child(2), .hexagon:nth-child(4) {
        width: 20vw;
        height: 20vw;
    }

    .hexagon:nth-child(3) {
        width: 30vw;
        height: 30vw;
    }

    .competitor_1, .competitor_2 {
        width: 35vw;
        height: 12.5vw;
        font-size: 3.5vw;
        clip-path: polygon(0 0, 0 100%, 84% 100%, 100% 0%, 100% 0);
        border-radius: 0px 5% 25% 0px;
    }

    .time, .place {
        font-size: 3vw;
    }

    .date {
        font-size: 4vw;
    }

    .buy_tickets {
        font-size: 2.3vw;
    }

    .hexagon:nth-child(1) {
        transform: translate(-40vw,26vw);
    }

    .hexagon:nth-child(2) {
        transform: translate(-23vw,15vw);
    }

    .hexagon:nth-child(4) {
        transform: translate(23vw,-15vw);
    }

    .hexagon:nth-child(5) {
        transform: translate(40vw,-26vw);
    }

    .illum {
        width: 70vw;
        height: 100vh;
    }

}
</style>